body {
    margin: 0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: antiquewhite;
    height: 100%;
}

html,body{
    height: 100%;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.movies .movie {
    width: 420px;
}